import { Injectable } from '@angular/core';
import { GetOwnUser, UserManagementService } from '@backbase/user-http-ang';
import { IdentitySelfServiceJourneyConfigurationService } from '../../identity-self-service-journey.config.service';
import { UserLocalizationJourneyConfiguration } from '../../identity-self-service-journey.types';
import { cacheItem } from '../../util';
import { localizationOptions } from './user-localization.config';

@Injectable()
export class UserLocalizationService {
  private readonly getUserCacheItem = cacheItem<GetOwnUser>(this.dataService.getOwnUser.bind(this.dataService));
  public readonly getUser$ = this.getUserCacheItem.state$;
  public readonly getUser = this.getUserCacheItem.update;

  public readonly defaultLocalizationOptions = localizationOptions;

  public readonly config: UserLocalizationJourneyConfiguration = Object.freeze({
    ...this.configService.userLocalization,
    notificationDismissTime: (this.configService.userLocalization.notificationDismissTime || 0) * 1000,
  });

  constructor(
    private readonly dataService: UserManagementService,
    /** @internal */
    readonly configService: IdentitySelfServiceJourneyConfigurationService,
  ) {}

  updateUser(userVO: Partial<GetOwnUser>) {
    return this.dataService.updateOwnLanguage({
      updatePreferredLanguage: {
        preferredLanguage: userVO.preferredLanguage || '',
      },
    });
  }
}
