import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { share } from 'rxjs/operators';
import { LoadableViewState } from '@backbase/identity-common-ang';
import { UserLocalizationService } from '../user-localization.service';
import { UserLocalizationOptionVM } from '../../../util';
import { userLanguageCommonViewStatePipe } from '../shared/user-language-common-view-state.pipe';

interface CommunicationsViewState extends LoadableViewState {
  languageRegion?: UserLocalizationOptionVM;
}

@Component({
  selector: 'bb-user-localization-communications-view',
  templateUrl: 'user-localization-communications-view.component.html',
})
export class UserLocalizationCommunicationsViewComponent {
  private readonly viewStateSubject$ = new BehaviorSubject<void>(undefined);
  readonly viewState$: Observable<CommunicationsViewState> = this.viewStateSubject$.pipe(
    userLanguageCommonViewStatePipe(this.userLocalizationService),
    share(),
  );

  constructor(
    private readonly router: Router,
    private readonly userLocalizationService: UserLocalizationService,
    private readonly activatedRoute: ActivatedRoute,
  ) {}

  changeLanguage() {
    this.router.navigate([this.activatedRoute.snapshot?.data?.changeLanguagePath || '../change-language'], {
      relativeTo: this.activatedRoute,
    });
  }

  getLanguageLabelFor(value: string) {
    return this.userLocalizationService.defaultLocalizationOptions.find((option) => option.value === value)?.label;
  }

  get showEditableLanguage() {
    return this.userLocalizationService.config.showEditableLanguage;
  }
}
