import { of, pipe } from 'rxjs';
import { catchError, map, startWith, switchMap } from 'rxjs/operators';
import { UserLocalizationService } from '../user-localization.service';
import { findLanguageRegionOption } from '../../../util/mappings/find-language-region-from-options';

export const userLanguageCommonViewStatePipe = (userLocalizationService: UserLocalizationService) =>
  pipe(
    switchMap(() =>
      userLocalizationService.getUser$.pipe(
        map((userProfile) => ({
          loading: false,
          languageRegion: findLanguageRegionOption(
            userLocalizationService.config.localizationOptions,
            userProfile.preferredLanguage,
          ),
        })),
        startWith({ loading: true }),
        catchError((error) => of({ loading: false, error })),
      ),
    ),
  );
