import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { provideRoutes, Route, RouterModule } from '@angular/router';
import { BackbaseCoreModule } from '@backbase/foundation-ang/core';

import { UserIdentitySecurityCenterViewComponent } from './features/user-identity-security-center-view.component';
import { UserManageProfileViewComponent } from './features/user-manage-profile-view.component';
import { DeviceInformationViewComponent } from './features/device-information-view.component';
import { UserLocalizationComponent } from './features/user-localization/user-localization.component';
import { UserLocalizationCommunicationsViewComponent } from './features/user-localization/communications-view/user-localization-communications-view.component';
import { UserLocalizationChangeLanguageViewComponent } from './features/user-localization/change-language-view/user-localization-change-language-view.component';
import { IdentitySelfServiceJourneyComponent } from './identity-self-service-journey.component';
import { IdentityCommonAngModule } from '@backbase/identity-common-ang';

import { SelfServiceFeaturesModule } from './features/self-service-features.module';
import { IdentitySelfServiceJourneyConfigurationService } from './identity-self-service-journey.config.service';

export const defaultRoute: Route = {
  path: '',
  component: IdentitySelfServiceJourneyComponent,
  children: [
    { path: '', redirectTo: 'profile', pathMatch: 'full' },
    {
      path: 'profile',
      data: {
        title: $localize`:Tab label for managing user profile@@bb-identity-self-service-journey.tab-user-manage-profile:Profile`,
      },
      component: UserManageProfileViewComponent,
    },
    {
      path: 'user-localization',
      data: {
        title: $localize`:Tab label for changing user preferences@@bb-identity-self-service-journey.tab-localization:Localization`,
      },
      component: UserLocalizationComponent,
      children: [
        { path: '', redirectTo: 'communications', pathMatch: 'full' },
        {
          path: 'communications',
          component: UserLocalizationCommunicationsViewComponent,
        },
        {
          path: 'change-language',
          component: UserLocalizationChangeLanguageViewComponent,
        },
      ],
    },
    {
      path: 'login-security',
      data: {
        title: $localize`:Tab label for login and security settings@@bb-identity-self-service-journey.tab-login-security:Login & security`,
      },
      component: UserIdentitySecurityCenterViewComponent,
    },
    {
      path: 'devices',
      data: { title: $localize`:Tab label for managing devices@@bb-identity-self-service-journey.tab-devices:Devices` },
      component: DeviceInformationViewComponent,
    },
  ],
};

@NgModule({
  imports: [
    CommonModule,
    RouterModule,
    BackbaseCoreModule.forRoot({
      classMap: {
        IdentitySelfServiceJourneyComponent,
      },
    }),
    IdentityCommonAngModule,
    SelfServiceFeaturesModule,
  ],
  declarations: [IdentitySelfServiceJourneyComponent],
  providers: [IdentitySelfServiceJourneyConfigurationService],
  exports: [RouterModule, IdentitySelfServiceJourneyComponent],
})
export class IdentitySelfServiceJourneyModule {
  static forRoot(
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    data: { route: Route; [key: string]: any } = { route: defaultRoute },
  ): ModuleWithProviders<IdentitySelfServiceJourneyModule> {
    return {
      ngModule: IdentitySelfServiceJourneyModule,
      providers: [provideRoutes([data.route])],
    };
  }
}
