import { Component, TemplateRef, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { generateId, LoadableViewState } from '@backbase/identity-common-ang';
import { UserLocalizationOptionVM, UserLocalizationSaveClickEventData } from '../../../util';
import { BehaviorSubject, Observable } from 'rxjs';
import { finalize, share, switchMap } from 'rxjs/operators';
import { UserLocalizationService } from '../user-localization.service';
import { NotificationService } from '@backbase/ui-ang/notification';
import { userLanguageCommonViewStatePipe } from '../shared/user-language-common-view-state.pipe';

interface ChangeLanguageViewState extends LoadableViewState {
  languageRegion?: UserLocalizationOptionVM;
}

@Component({
  selector: 'bb-user-localization-change-language-view',
  templateUrl: 'user-localization-change-language-view.component.html',
})
export class UserLocalizationChangeLanguageViewComponent {
  @ViewChild('notificationChangeLanguageSuccess') notificationChangeLanguageSuccess: TemplateRef<any> | undefined;
  @ViewChild('notificationChangeLanguageSuccessHeader') notificationChangeLanguageSuccessHeader:
    | TemplateRef<any>
    | undefined;
  @ViewChild('notificationFailure') notificationFailure: TemplateRef<any> | undefined;

  modalOpen = false;

  readonly isSaveLoadingSubject$ = new BehaviorSubject(false);

  private readonly viewStateSubject$ = new BehaviorSubject<void>(undefined);
  readonly viewState$: Observable<ChangeLanguageViewState> = this.viewStateSubject$.pipe(
    userLanguageCommonViewStatePipe(this.userLocalizationService),
    share(),
  );

  readonly ids = {
    languageRegionLabel: generateId('language-region'),
  };

  constructor(
    private readonly userLocalizationService: UserLocalizationService,
    private readonly router: Router,
    private readonly notificationService: NotificationService,
    private readonly activatedRoute: ActivatedRoute,
  ) {}

  public saveChanges({ preferredLanguage }: UserLocalizationSaveClickEventData) {
    this.isSaveLoadingSubject$.next(true);

    this.userLocalizationService
      .updateUser({ preferredLanguage })
      .pipe(
        switchMap(() => this.userLocalizationService.getUser()),
        finalize(() => this.isSaveLoadingSubject$.next(false)),
      )
      .subscribe({
        next: () => {
          this.showNotification(
            this.notificationChangeLanguageSuccess as TemplateRef<any>,
            this.notificationChangeLanguageSuccessHeader as TemplateRef<any>,
          );
          this.navigateCommunications();
        },
        error: () => {
          this.showNotification(this.notificationFailure as TemplateRef<any>, undefined, 'error');
        },
      });
  }

  cancelChangeLanguage() {
    this.modalOpen = true;
  }

  cancelModal() {
    this.modalOpen = false;
  }

  discardChanges() {
    this.modalOpen = false;
    this.navigateCommunications();
  }

  navigateCommunications() {
    this.router.navigate([this.activatedRoute.snapshot?.data?.communicationsPath || '../communications'], {
      relativeTo: this.activatedRoute,
    });
  }

  private showNotification(message: TemplateRef<any>, header?: TemplateRef<any>, modifier?: 'error'): void {
    this.notificationService.showNotification({
      header,
      message,
      modifier: modifier || 'success',
      ttl: this.userLocalizationService.config.notificationDismissTime,
    });
  }

  get localizationOptions() {
    return this.userLocalizationService.config.localizationOptions;
  }
}
